"use server";

import { GoogleGenerativeAI } from "@google/generative-ai";

const genAI = new GoogleGenerativeAI(process.env.GEMINI_API_KEY);
const model = genAI.getGenerativeModel({ model: "gemini-2.5-flash" });

/**
 * Generate a personalized AI learning feed
 * @param {Object} params - { industry: string, skills: string[], interests: string[] }
 * @returns {Promise<Array>} feed items in JSON format
 */
export async function LearningFeed({ industry, skills, interests }) {
  if (!industry && (!skills || skills.length === 0) && (!interests || interests.length === 0)) {
    throw new Error("Please provide at least one field (industry, skills, interests).");
  }

  const prompt = `
  Create a high-quality personalized learning feed for a user with the following:
  Industry: ${industry || "General"}  
  Skills: ${skills?.join(", ") || "None"}  
  Interests: ${interests?.join(", ") || "General Knowledge"}  

  Generate exactly 5 items. Each item must include:
  - title: A clear, engaging title
  - type: Article, Video, or Podcast
  - source: Name of the website, platform, or creator (reputable sources like Medium, TED, YouTube)
  - time: Estimated consumption time (e.g., "5 min read", "15 min video")
  - summary: 3 concise bullet points with actionable insights
  - relevance: Why this is useful for the user's industry, skills, or interests
  - link: A valid URL (use realistic placeholders like https://example.com/item1, item2, etc.)

  Requirements:
  - Use professional, informative, and engaging language
  - Summaries should be clear and actionable, 1-2 sentences per bullet
  - Respond strictly in valid JSON array format, no extra text or explanations
  - Ensure all URLs are valid
  `;

  try {
    const result = await model.generateContent(prompt);
    const text = result.response.text().trim();

    // Parse JSON safely
    const feed = JSON.parse(text);
    if (!Array.isArray(feed)) throw new Error("AI output is not a JSON array");
    return feed;
  } catch (err) {
    console.error("Failed to generate learning feed:", err, "\nAI output:", err?.response?.text?.() || "");
    throw new Error("Failed to generate learning feed. Make sure the AI output is valid JSON.");
  }
}
